import React from 'react'
import Rank from './rank';

const Skills = ({item}) => {
    return (
        <div style={{
            display: 'flex',
            flexDirection: 'column',
            width: '140',
            padding: '1rem'
        }}>
            <h4>{item.type}</h4>
            {item.items.map((skill) => {
                return (
                    <div style={{
                            display: 'flex', 
                            justifyContent:'space-between', 
                            padding:'0.4rem'
                        }}key={skill.name}>
                        <span style={{width:'70px'}}>{skill.name}</span>
                        <div style={{width:'80px'}}><Rank value={skill.value} /></div>
                    </div>
                )
            })}
        </div>
    )
}

export default Skills